import { customElement, property } from 'lit/decorators.js';
import { classMap } from 'lit/directives/class-map.js';
import { html, TemplateResult } from 'lit';
import { Base } from '../base';

type Theme = 'white' | 'light' | 'dark';

@customElement('zwc-tag')
export class ZwcTag extends Base {
  @property({ type: String, reflect: true })
  label = 'Label';

  /**
   * The theme name to apply.
   */
  @property({ type: String, reflect: true })
  theme: Theme = 'white';

  get key(): string {
    return `tag-${this.label.toLocaleLowerCase().split(' ').join('-')}`;
  }

  override render(): TemplateResult {
    const classes = {
      tag: true,
      'tags-control': true,
      [`tags-control--${this.theme}`]: true,
    };

    return html`
      <div class=${classMap(classes)}>
        <input
          type="checkbox"
          id="${this.key}"
          name="${this.key}"
          checked
          @change=${this.emitChange}
        />
        <label for="${this.key}">${this.label}</label>
      </div>
    `;
  }

  emitChange(): void {
    this.dispatchEvent(
      new CustomEvent('zwcTagChange', {
        composed: true,
        cancelable: true,
        bubbles: true,
      }),
    );
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'zwc-tag': ZwcTag;
  }
}
